'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/router';

export default function Checkout() {
  const [product, setProduct] = useState(null);
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [total, setTotal] = useState(0);
  const router = useRouter();
  const { id } = router.query;

  useEffect(() => {
    if (id) {
      const fetchProductData = async () => {
        try {
          const response = await fetch(`http://127.0.0.1:8000/get_products/?id=${id}`);
          const data = await response.json();
          if (data.products && data.products.length > 0) {
            setProduct(data.products[0]);
            setTotal(data.products[0].product_price);
          } else {
            console.error('Product not found');
          }
        } catch (error) {
          console.error('Error fetching product details:', error);
        }
      };
      fetchProductData();
    }
  }, [id]);

  const handleSubmit = (e) => {
    e.preventDefault();
    // You can handle the submission here
    console.log("Order placed:", { name, email, phone, product });
  };

  if (!product) {
    return (
      <div className="flex items-center justify-center h-screen bg-gray-100">
        <p>Loading product details...</p>
      </div>
    );
  }

  return (
    <div className="p-8">
      <h1 className="text-4xl font-bold text-center">Checkout</h1>

      <div className="flex justify-between mt-8">
        {/* Left Section: Product Details */}
        <div className="w-2/3 bg-gray-100 p-4 rounded-lg">
          <h3 className="text-2xl font-semibold">{product.product_name}</h3>
          <p><strong>Price:</strong> {product.product_price} ฿</p>
          <p><strong>Description:</strong> {product.product_description}</p>
          <p><strong>Rank:</strong> {product.product_rank}</p>
        </div>

        {/* Right Section: Order Form */}
        <div className="w-1/3 bg-white p-4 rounded-lg shadow-md">
          <h3 className="text-2xl font-semibold mb-4">Order Information</h3>
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700">Full Name</label>
              <input
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
                className="mt-2 w-full p-3 border border-gray-300 rounded-md focus:outline-none"
                placeholder="Enter your name"
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700">Email</label>
              <input
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="mt-2 w-full p-3 border border-gray-300 rounded-md focus:outline-none"
                placeholder="Enter your email"
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700">Phone Number</label>
              <input
                type="tel"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                className="mt-2 w-full p-3 border border-gray-300 rounded-md focus:outline-none"
                placeholder="Enter your phone number"
                required
              />
            </div>

            <div className="mt-4 text-right">
              <p><strong>Total: </strong>{total} ฿</p>
            </div>

            <div className="text-center mt-6">
              <button type="submit" className="bg-green-600 text-white px-6 py-2 rounded-md hover:bg-green-500">
                Confirm Donation
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
